import Vue from 'vue'
import Router from 'vue-router'
import EditOtd from '@/components/EditOtd'
import Upload from '@/components/Upload'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/editOtd',
      name: 'EditOtd',
      component: EditOtd
    },
    {
      path: '/UploadFile',
      name: 'Upload',
      component: Upload
    }
  ]
})



